import React from "react";
import AdminDrawer from "./AdminDrawer";
import { withRouter } from "react-router-dom";
import {
  showAllCourses,
  getInstructorByCompanyId,
  getAllCategory
} from "../../Actions/adminActions";
import { connect } from "react-redux";
import "./AddCourse.css";
import Loader from "../../Loader";
import isEmpty from "../Protected/validation/is-empty";
import "./Courses.css";
class Courses extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      courseImage: null,
      desc: "",
      descPlaceholder: "Insert your course description",
      introduction: "",
      introductionPlaceholder: "Insert your course description",
      whatwillyoulearn: "",
      whatwillyoulearnPlaceholder: "Seperate by commas",
      instructor: "",
      videomedium: "youtube",
      batchDuration: 0,
      batchDurationPlaceholder: "Batch duration",
      level: "",
      category: "",
      language: "",
      price: 0,
      pricePlaceholder: "Insert your course price",
      blank: "",
      errors: {},
      loading: false,
      currentCourse: {}
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.showallcoursesbycompany) {
      this.setState({
        courses: nextProps.showallcoursesbycompany
      });
    }
  }

  componentDidMount() {
    this.props.showAllCourses();
    this.props.getInstructorByCompanyId();
    this.props.getAllCategory();
  }

  handleImage = event => {
    if (event.target.files && event.target.files[0]) {
      this.setState({
        courseImage: URL.createObjectURL(event.target.files[0]),
        courseImageURL: event.target.files[0]
      });
    }
  };

  handleInputChange = event => {
    this.setState({ errors: {} });

    this.setState({
      [event.target.name]: event.target.value
    });
  };

  onClick = e => {
    e.preventDefault();
    const errors = {};

    if (isEmpty(this.state.introduction)) {
      errors.introduction = "Introduction/Title is Required";
    }
    if (isEmpty(this.state.desc)) {
      errors.desc = "Description is Required";
    }
    if (isEmpty(this.state.whatwillyoulearn)) {
      errors.whatwillyoulearn = "This field is Required";
    }
    if (isEmpty(this.state.category)) {
      errors.category = "Category field is Required";
    }
    if (isEmpty(this.state.instructor)) {
      errors.instructor = "Instructor field is Required";
    }
    if (isEmpty(this.state.price) || this.state.price === 0) {
      errors.price = "Price field is Required";
    }
    if (isEmpty(this.state.batchDuration) || this.state.batchDuration === 0) {
      errors.batchDuration = "Batch Duration is Required";
    }

    this.setState({ errors });
    if (isEmpty(errors)) {
      this.setState({ loading: true })
    //   this.props.addCourseMain(values, this.props.history);
    }
  };

  render() {
    return isEmpty(this.props.showallcoursesbycompany) ||
      isEmpty(this.props.getallcategory) ||
      isEmpty(this.props.getinstructorbycompanyid) ? (
      <Loader />
    ) : (
      <AdminDrawer>
        <div className="container">
          {this.state.courses.map(course => (
            <div className="card mb-4" key={course.courseid}>
              <h5 className="card-header">{course.CourseIntroduction}</h5>
              <div className="card-body">
                <h5 className="card-title">{`Instructor: ${
                  course.instructorName
                }`}</h5>
                <p className="card-text">{course.CourseDescription}</p>
                <a
                  href="#"
                  className="btn btn-primary mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() =>
                    this.setState({ 
                       introduction : course.CourseIntroduction,
                       desc: course.CourseDescription,
                       whatwillyoulearn: course.whatwillyoulearn,
                       instructor: course.instructorid,
                       batchDuration: course.batchDuration,
                       price: course.Fee
                    })
                  }
                >
                  Edit
                </a>
                <a
                  href="#"
                  className="btn btn-danger"
                  data-toggle="modal"
                  data-target="#deleteModal"
                  onClick={() =>
                    this.setState({ currentCourse: course })
                  }
                >
                  Delete
                </a>
              </div>
            </div>
          ))}
          {/* Edit Modal Start */}
          <div
            className="modal fade modalHarami"
            id="editModal"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="editModalLabel"
            aria-hidden="true"
          >
            <div
              className="modal-dialog modal-dialog-scrollable"
              role="document"
            >
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="editModalLabel">
                    Edit Course
                  </h5>
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body">
                  {/* Edit Modal Body */}

                  <div>
                    <label className="labels pt-4">
                      <span className="imp">*&nbsp;</span>Course Introduction
                    </label>
                    <div className="input-group">
                      <input
                        type="text"
                        name="introduction"
                        value={this.state.introduction}
                        className="form-control"
                        onChange={this.handleInputChange}
                        maxLength={80}
                        placeholder={this.state.introductionPlaceholder}
                      />
                      <div className="input-group-append">
                        <span className="input-group-text">
                          {this.state.introduction.length < 80
                            ? 80 - this.state.introduction.length
                            : 0}
                        </span>
                      </div>
                    </div>
                  </div>
                  {this.state.errors.introduction && (
                    <div className="imp textSize">
                      {this.state.errors.introduction}
                    </div>
                  )}

                  <div>
                    <label className="labels pt-4">
                      <span className="imp">*&nbsp;</span>Course Instructor
                    </label>
                    <div className="input-group">
                      <select
                        name="instructor"
                        value={this.state.instructor}
                        className="form-control inputBasicInfo"
                        onChange={this.handleInputChange}
                        maxLength={60}
                      >
                        <option value="">Select Instructor</option>
                        {this.props.getinstructorbycompanyid.map(instructor => (
                          <option
                            key={instructor.instructorid}
                            value={instructor.instructorid}
                          >
                            {instructor.instructorName}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                  {this.state.errors.instructor ? (
                    <div className="imp textSize">
                      {this.state.errors.instructor}
                    </div>
                  ) : null}

                  <div>
                    <label className="labels pt-4">
                      <span className="imp">*&nbsp;</span>Course description
                    </label>
                    {/* <EditorDesc
                    handleEditor={this.handleEditor}
                    placeholder={this.state.descPlaceholder}
                  /> */}
                    <textarea
                      type="text"
                      name="desc"
                      value={this.state.desc}
                      className="form-control"
                      onChange={this.handleInputChange}
                      placeholder={this.state.descPlaceholder}
                    />
                  </div>
                  {this.state.errors.desc ? (
                    <div className="imp textSize">{this.state.errors.desc}</div>
                  ) : null}
                  <div>
                    <label className="labels pt-4">
                      <span className="imp">*&nbsp;</span>What will student
                      learn in this course
                    </label>
                    <div className="input-group">
                      <textarea
                        type="text"
                        name="whatwillyoulearn"
                        value={this.state.whatwillyoulearn}
                        className="form-control"
                        onChange={this.handleInputChange}
                        maxLength={80}
                        placeholder={this.state.whatwillyoulearnPlaceholder}
                      />
                    </div>
                  </div>
                  {this.state.errors.whatwillyoulearn ? (
                    <div className="imp textSize">
                      {this.state.errors.whatwillyoulearn}
                    </div>
                  ) : null}

                  <div>
                    <label className="labels pt-4">
                      <span className="imp">*&nbsp;</span>Batch Duration
                    </label>
                    <div className="input-group">
                      <input
                        type="number"
                        name="batchDuration"
                        value={this.state.batchDuration}
                        className="form-control"
                        onChange={this.handleInputChange}
                        maxLength={80}
                        placeholder={this.state.batchDurationPlaceholder}
                      />
                    </div>
                  </div>
                  {this.state.errors.batchDuration && (
                    <div className="imp textSize">
                      {this.state.errors.batchDuration}
                    </div>
                  )}

                  <div>
                    <label className="labels pt-4">
                      <span className="imp">*&nbsp;</span>Course Pricing
                    </label>
                    <div className="input-group">
                      <input
                        type="number"
                        name="price"
                        value={this.state.price}
                        className="form-control"
                        onChange={this.handleInputChange}
                        maxLength={80}
                        placeholder={this.state.price}
                      />
                    </div>
                  </div>
                  {this.state.errors.price && (
                    <div className="imp textSize">
                      {this.state.errors.price}
                    </div>
                  )}
                  {/* Edit Modal Body End */}
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-secondary"
                    data-dismiss="modal"
                  >
                    Close
                  </button>
                  <button type="button" className="btn btn-primary" onClick={this.onClick}>
                    Save changes
                  </button>
                </div>
              </div>
            </div>
          </div>
          {/* Edit Modal End */}
          {/* Delete Modal Start */}
          <div
            className="modal fade modalHarami"
            id="deleteModal"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="deleteModalLabel"
            aria-hidden="true"
          >
            <div
              className="modal-dialog modal-dialog-scrollable"
              role="document"
            >
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="deleteModalLabel">
                    Delete Course
                  </h5>
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body">...</div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-secondary"
                    data-dismiss="modal"
                  >
                    Close
                  </button>
                  <button type="button" className="btn btn-danger">
                    Delete
                  </button>
                </div>
              </div>
            </div>
          </div>
          {/* Delete Modal End */}
        </div>
      </AdminDrawer>
    );
  }
}

const mapStateToProps = state => ({
  showallcoursesbycompany: state.admin.showallcoursesbycompany,
  getallcategory: state.admin.getallcategory,
  getinstructorbycompanyid: state.admin.getinstructorbycompanyid
});
export default withRouter(
  connect(
    mapStateToProps,
    { getInstructorByCompanyId, getAllCategory, showAllCourses }
  )(Courses)
);
