import { convertFromRaw, convertToRaw } from 'draft-js';
import axios from "axios";
import { url } from "../url";
import { decryptId } from "../utils"

export const showAllCourses = () => dispatch => {
    axios
        .post(
            `${url}ShowAllCourses?CompanyId=${decryptId()}`)
        .then(res => {
            dispatch({
                type: "SHOW_ALL_COURSES_BY_COMPANY",
                payload: res.data
            });
        })
        .catch(err => console.log(err));
}

export const getAllCategory = () => dispatch => {
    axios
        .get(
            `${url}GetAllCategory`)
        .then(res => {
            dispatch({
                type: "GET_ALL_CATEGORY",
                payload: res.data
            });
        })
        .catch(err => console.log(err));
}

export const saveInstructor = (name, desc, image, history) => dispatch => {
    axios
    .post(
        `${url}SaveInstructor?InstructorName=${name}&Description=${desc}&CompanyId=${decryptId()}`)
    .then(res => {
        let instructorid = res.data.slice(-1)[0].instructorid;
        // postFile(courseid, instructorid, res.data, values.courseImage)

        var bodyFormData = new FormData();
        bodyFormData.append('', image);
        axios.post(`${url}PostFile?CompanyId=${decryptId()}&CourseId=0&InstructorId=${parseInt(instructorid)}`,
            bodyFormData,
            {
                headers: {
                    'Content-Type': `multipart/form-data`,
                }
            }
        ).then(resFinal => {
            console.log("saved")
            history.push('/adashboard')
            dispatch({
                type: "SAVE_INSTRUCTOR",
                payload: res.data
            });
        })
            .catch(err => console.log(err.response.data));
    })
    .catch(err => console.log(err));
}

