const inititalState = {}

export default function (state = inititalState, action) {
    switch (action.type) {
        case "GET_ALL_CATEGORY":
            return {
                ...state,
                getallcategory: action.payload
            };
        case "GET_INSTRUCTOR_BY_COMPANYID":
            return {
                ...state,
                getinstructorbycompanyid: action.payload
            };
        case "POST_FILE":
            return {
                ...state,
                postfile: action.payload
            };
        case "ADD_COURSE_MAIN":
            return {
                ...state,
                addcoursemain: action.payload
            };
        case "GET_INSTRUCTOR_BY_ID":
            return {
                ...state,
                getinstructorbyid: action.payload
            };
        case "SAVE_INSTRUCTOR":
            return {
                ...state,
                saveinstructor: action.payload
            };
        case "SHOW_ALL_COURSES_BY_COMPANY":
            return {
                ...state,
                showallcoursesbycompany: action.payload
            };
        default:
            return state;
    }
}